import request from "superagent/superagent";
import * as actionTypes from './actionTypes';
import * as constant from './../../shared/constant';
import * as utility from './../../shared/utility';
import * as action from './index';


export const getAdditionalQuestions = () => {
    return dispatch => {
        dispatch(action.restCallStart());

        var payloadArray = [];
        request
            .post(constant.GET_DYNAMIC_QUESTIONS_TARGET_URL)
            .set('Content-Type', 'application/json')
            .set('Accept', 'application/json')
            .auth('pamAdmin', 'redhatpam1!')
            .send(constant.POST_DYNAMIC_QUESTION_PAYLOAD)
            .then((res) => {
                dispatch(action.restCallSuccess());
                var tmp = {};
                tmp.status = res.status;
                tmp.body = res.body;
                //    console.log(tmp.body);
                payloadArray = utility.processJson(tmp.body);
                //console.log(payloadArray);
                return dispatch({ type: actionTypes.GET_ADDITIONAL_QUESTIONS, payload: payloadArray });
            })
            .catch((err) => {
                var tmp = {};

                tmp.errorMessage = err.message;
                tmp.errorResponse = err.response;
                dispatch(action.restCallFail(tmp.errorResponse));
            });
    }
}

export const submitDispute = (username) => {

    ///////mocking user profile ///////
    console.log(username);
    var payload = {};
    if (username === 'jonSnow') {

        payload = constant.JON_SNOW_START_CASE_PAYLOAD
    } else if (username === 'khalDrogo') {

        payload = constant.KHAL_DROGO_START_CASE_PAYLOAD
    } else if (username === 'aryaStark') {

        payload = constant.ARYA_STARK_START_CASE_PAYLOAD
    } else {

        payload = constant.START_CASE_PAYLOAD
    }

    //////////////////////////////////

    return dispatch => {
        dispatch(action.restCallStart());

        var payloadArray = [];
        request
            .post(constant.POST_START_CASE_TARGET_URL)
            .set('Content-Type', 'application/json')
            .set('Accept', 'application/json')
            .auth('pamAdmin', 'redhatpam1!')
            .send(payload)
            .then((res) => {
                dispatch(action.restCallSuccess());
                var tmp = {};
                tmp.status = res.status;
                tmp.body = res.body;
                //    console.log(tmp.body);
                payloadArray = tmp.body;
                console.log(payloadArray);
                dispatch(action.submitDisputeResponse(payloadArray.caseId));
            })
            .catch((err) => {
                var tmp = {};

                console.log(err);
                tmp.errorMessage = err.message;
                tmp.errorResponse = err.response;
                dispatch(action.restCallFail(tmp.errorResponse));
            });

    }
}

export const remoteGetUserProfile = (accessToken) => {
    return dispatch => {
        dispatch(action.restCallStart());

        console.log("Action " + constant.GET_USER_PROFILE_URL);
        request
            .get(constant.GET_USER_PROFILE_URL)
            .set('Content-Type', 'application/json')
            .set('Accept', 'application/json')
            .set('Authorization', 'Bearer ' + accessToken)
            .then((res) => {
                dispatch(action.restCallSuccess());

                const response = JSON.parse(res.body);
                console.log("Server Response " + JSON.stringify(response));

                return dispatch(action.userProfile(response));
            })
            .catch((err) => {
                var tmp = {};
                tmp.errorMessage = err.message;
                tmp.errorResponse = err.response;
                console.log(tmp);
                console.log(err);
                dispatch(action.restCallFail(tmp.errorResponse));
            });
    }
}


export const remoteGetActivityLog = (accessToken, caseId) => {
    return dispatch => {
        dispatch(action.restCallStart());

        console.log("Action " + constant.GET_ACTIVITY_LOG);
        request
            .get(constant.GET_ACTIVITY_LOG + caseId + '/activitylog')
            .set('Content-Type', 'application/json')
            .set('Accept', 'application/json')
            .set('Authorization', 'Bearer ' + accessToken)
            .then((res) => {
                dispatch(action.restCallSuccess());

                // const response = res.body;
                // console.log("Server Response " + JSON.stringify(response));

                var tmp = {};

                var nodeArray = res.body["node-instance"];

                // console.log("Server Response " + JSON.stringify(nodeArray));

                for (var i = 0; i < nodeArray.length; i++) {
                    var javaDate = res.body["node-instance"][i]["start-date"]["java.util.Date"];
                    var newDate = new Date(javaDate);
                    newDate = newDate.toLocaleString();
                    tmp.date = newDate;
                    tmp.activity = res.body["node-instance"][i]["node-name"];
                    tmp.id = res.body["node-instance"][i]["node-id"];
                    tmp.type = res.body["node-instance"][i]["node-type"];
                    tmp.completed = res.body["node-instance"][i]["node-completed"];
                    console.log('tmp ' + JSON.stringify(tmp));
                }


                dispatch(action.setActivityLog(tmp));
            })
            .catch((err) => {
                var tmp = {};
                tmp.errorMessage = err.message;
                tmp.errorResponse = err.response;
                console.log(tmp);
                console.log(err);
                dispatch(action.restCallFail(tmp.errorResponse));
            });
    }
}


