/* eslint no-unused-vars: 0, eqeqeq:0 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../state/actions/index';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';

const ProductTable = props => {

  const data = props.data;
  const dataArray = [];
  dataArray.push(data);

  console.log(dataArray);

  return (
    <div>
      <BootstrapTable data={dataArray}
        bordered={false}
        striped
        search={true}
        pagination>
        <TableHeaderColumn isKey dataField='id' dataSort={true} hidden>ID</TableHeaderColumn>
        <TableHeaderColumn dataField='date' dataSort={true} dataAlign='left'>Date</TableHeaderColumn>
        <TableHeaderColumn dataField='activity' dataSort={true} dataAlign='center'>Activity</TableHeaderColumn>
        <TableHeaderColumn dataField='type' dataSort={true} dataAlign='center'>Type</TableHeaderColumn>
      </BootstrapTable>
    </div>

  );

}


class TransactionHistoryApp extends Component {

  componentDidMount() {
    const accessToken = localStorage.getItem("accessToken");
    // const accessToken = "ab222fdf7027d3d3e498b88059e36f0f";
    const caseId = localStorage.getItem("caseId");
    // const caseId = 'FR-0000000032';

    if (accessToken != null && caseId != null) {
      this.props.remoteGetActivityLog(accessToken, caseId);
    }
  }


  render() {
    const caseActivityLog = this.props.caseActivityLog;
    return (
      <div className="row">
        <div className="col-lg-12">
          <div className="ibox float-e-margins">
            <div className="ibox-title bluetop">
              <h2>Activity Log</h2>
            </div>
            <div className="ibox-content" id="main-contents">
              <div className="row">
                <div className="col-md-12">
                  {
                    caseActivityLog != null ?
                  <ProductTable
                    data={caseActivityLog}
                  />
                  :
                  null
                  }
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    caseActivityLog: state.transactionDisputeReducer.caseActivityLog,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    remoteGetActivityLog: (accessToken, caseId) => dispatch(actions.remoteGetActivityLog(accessToken, caseId)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(TransactionHistoryApp);


